import { useState } from "react";
import AuthIcon from "./icons/Auth.png"
import { useEffect } from "react";
function Step() {
    const [check, setCheck] = useState("step-icon");
    const [title, setTitle] = useState("");
    const [inf, setInf] = useState<any>("");
    const [image, setImage] = useState<any>();

    const firstStep = {
        title: "Authentication ",
        inf: <div>
                   .<br /><br />
            1.           .<br /> <br />
            2.         .<br /><br />
            3.   3~4    . .<br /><br />
            4.        .
        </div>,
        image: AuthIcon
    }
    const secondStep = {
        title: "Data Collect ",
        inf: <div>
                    Eye Tracking    .<br /><br />
            1.	   Data Collect      .<br /><br />
            2.	               .<br /><br />
            3.	 9   5    .<br /><br />
            4.	 14                 .<br />
        </div>,
        image: AuthIcon
    }

    const thirdStep = {
        title: "Screen Sharing ",
        inf: "Screen Sharing    .",
        image: AuthIcon
    }

    const prevHandler = () => {
        if (check === "step-icon") {
            setCheck("step-icon")
            setTitle(firstStep.title)
            setInf(firstStep.inf)
            setImage(firstStep.image)
        } else if (check === "step-icon2") {
            setCheck("step-icon")
            setTitle(firstStep.title)
            setInf(firstStep.inf)
            setImage(firstStep.image)
        } else if (check === "step-icon3") {
            setCheck("step-icon2")
            setTitle(secondStep.title)
            setInf(secondStep.inf)
            setImage(secondStep.image)
        }
    }

    const nextHandler = () => {
        if (check === "step-icon") {
            setCheck("step-icon2")
            setTitle(secondStep.title)
            setInf(secondStep.inf)
            setImage(secondStep.image)
        } else if (check === "step-icon2") {
            setCheck("step-icon3")
            setTitle(thirdStep.title)
            setInf(thirdStep.inf)
            setImage(thirdStep.image)
        } else if (check === "step-icon3") {
            setCheck("step-icon3")
            setTitle(thirdStep.title)
            setInf(thirdStep.inf)
            setImage(thirdStep.image)
        }
    }
    useEffect(() => {
        prevHandler();
    }, []);
    return (
        <div className="second-block">
            <div className="step">
                <div className="step-big-title">
                        
                </div>
                <div className="icon-space">
                    <div></div>
                    <div className={check}></div>
                    <div></div>
                </div>
                <div className="step-content">
                    <div className="step-left">
                        <div className="step-title">{title}</div>
                        <div className="step-information">
                            {inf}
                        </div>
                        <div>
                            <div className="step-btn" onClick={prevHandler}></div>
                            <div className="step-btn" onClick={nextHandler}></div>
                        </div>
                    </div>
                    <div className="step-right"><img src={image} width="300" /></div>
                </div>
            </div>
        </div>
    )
}

export default Step;


